"use strict";

var $ = require("jquery"),
    yutils = require("yasgui-utils"),
    imgs = require("./imgs.js");

var checkSyntax = function checkSyntax(yashe) {
  var defPrefixes = [];
  var prefixes = [];
  var shapes = [];
  var shapeRefs = [];
  yashe.queryValid = true;
  yashe.clearGutter("gutterErrorBar");
  var state = null;
  var openTokensCounter = 0;
  var closeTokensCounter = 0;

  for (var l = 0; l < yashe.lineCount(); ++l) {
    var precise = false;

    if (!yashe.prevQueryValid) {
      // we don't want cached information in this case, otherwise the
      // previous error sign might still show up,
      // even though the syntax error might be gone already
      precise = true;
    }

    var token = yashe.getTokenAt({
      line: l,
      ch: yashe.getLine(l).length
    }, precise);
    var state = token.state;

    if (state.OK == false) {
      if (!yashe.options.syntaxErrorCheck) {
        //the library we use already marks everything as being an error. Overwrite this class attribute.
        $(yashe.getWrapperElement()).find(".sp-error").css("color", "black"); //we don't want to gutter error, so return

        return;
      }

      var warningEl = yutils.svg.getElement(imgs.warning);

      if (state.errorMsg) {
        require("./tooltipUtils.js").grammarTootlip(yashe, warningEl, function () {
          return $("<div/>").text(token.state.errorMsg).html();
        });
      } else if (state.possibleCurrent && state.possibleCurrent.length > 0) {
        //				warningEl.style.zIndex = "99999999";
        require("./tooltipUtils.js").grammarTootlip(yashe, warningEl, function () {
          var expectedEncoded = [];
          state.possibleCurrent.forEach(function (expected) {
            expectedEncoded.push("<strong style='text-decoration:underline'>" + $("<div/>").text(expected).html() + "</strong>");
          });
          return "This line is invalid. Expected: " + expectedEncoded.join(", ");
        });
      }

      warningEl.style.marginTop = "2px";
      warningEl.style.marginLeft = "2px";
      warningEl.className = "parseErrorIcon";
      yashe.setGutterMarker(l, "gutterErrorBar", warningEl);
      yashe.queryValid = false;
      return false;
    } //This is only necessary to verify the if the last '}' is missing  (See #104)


    var lineTokens = yashe.getLineTokens(l); //Get all the defined prefixes and all the used prefixes
    //Get all the defined shapes and all the used shapes

    for (var t in lineTokens) {
      var _token = lineTokens[t];

      if (_token.string == '{') {
        openTokensCounter++;
      }

      if (_token.string == '}') {
        closeTokensCounter++;
      }

      if (_token.type == 'string-2' || _token.type == 'constraint') {
        prefixes.push({
          alias: _token.string.split(":")[0] + ':',
          line: l
        });
      }

      if (_token.type == 'valueSet') {
        if (_token.string.includes(":")) {
          prefixes.push({
            alias: _token.string.split(":")[0] + ':',
            line: l
          });
        }
      }

      if (_token.type == 'prefixDelcAlias') {
        defPrefixes.push(_token.string);
      }

      if (_token.type == 'shape') {
        shapes.push(_token.string);
      }

      if (_token.type == 'shapeRef') {
        shapeRefs.push({
          ref: _token.string.slice(1, _token.string.length),
          line: l
        });
      } //Necesary when the ShapeRef is "@:"


      if (_token.string == '@') {
        shapeRefs.push({
          ref: '@:',
          line: l
        });
      }
    }
  } //Check if the Prefixes are defined


  for (var p in prefixes) {
    var err = true;

    for (var d in defPrefixes) {
      if (defPrefixes[d] == prefixes[p].alias) err = false;
    }

    if (err) {
      setError(prefixes[p].line, "Prefix '" + prefixes[p].alias + "' is not defined");
      yashe.queryValid = false;
      return false;
    }
  } //Check if the ShapeRefs are defined


  for (var r in shapeRefs) {
    var _err = true;

    for (var s in shapes) {
      if (shapes[s] == shapeRefs[r].ref) _err = false;
    }

    if (_err) {
      setError(shapeRefs[r].line, "Shape '" + shapeRefs[r].ref + "' is not defined");
      yashe.queryValid = false;
      return false;
    }
  } // Is last '}' missing?  (See #104)


  if (openTokensCounter != closeTokensCounter) {
    setError(l, "This line is invalid. Expected: '}'");
    yashe.queryValid = false;
    return false;
  }

  yashe.prevQueryValid = yashe.queryValid;
  return true;
};

var setError = function setError(line, errMsg) {
  var warningEl = yutils.svg.getElement(imgs.warning);

  require("./tooltipUtils.js").grammarTootlip(yashe, warningEl, function () {
    return errMsg;
  });

  warningEl.style.marginTop = "2px";
  warningEl.style.marginLeft = "2px";
  warningEl.className = "parseErrorIcon";
  yashe.setGutterMarker(line, "gutterErrorBar", warningEl);
};

module.exports = {
  checkSyntax: checkSyntax
};